.projects {
  
}

.project {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(2, 1fr);
  position: relative;
  background: #ffffff;
  z-index: 1;
  
  @media screen and (min-width: 768px) {
    grid-template-rows: repeat(4, minmax(100px, 250px));
  }
  
  @media screen and (min-width: 1200px) {
    padding-top: 6em;
  }
}

.project__text {
  display: flex;
  grid-column: 1 / -1;
  grid-row: 4 / -1;
  position: relative;
  flex-direction: row;
  justify-content: center;
  padding: 2em 2em 50px 50px;
  background: #efefef;
  z-index: 2;
  
  @media screen and (min-width: 768px) {
    grid-column: 1 / 7;
    grid-row: 2 / span 2;
  }
  
  @media screen and (min-width: 1365px) {
    grid-column: 1 / 6;
  }
}

.project__count {
  grid-column: 2 / span 2;
  grid-row: 3 / span 1;
  font-size: 12em;
  font-weight: 800;
  margin-top: -0.15em;

  @media screen and (min-width: 768px) {
    grid-row: 3 / span 2;
  }

  @media screen and (min-width: 1200px) {
    grid-column: 2 / span 2;
    grid-row: 2 / span 2;
    font-size: 20em;
  }
}

.project__view {
  position: absolute;
  bottom: 0;
  left: 0;
}

.project__text__wrap {
  // width: 66%;
  flex: 1;
  padding-right: 0.5em;
  
  @media screen and (min-width: 768px) {
    
    flex: 2;
  }
}

.project__tech {
  flex: 1;
}

.project__title {
  font-size: 3em;
  font-weight: 700;
  margin-top: 1em;
  margin-bottom: 0.33em;
  grid-column: 1 / span 2;
  
  @media screen and (min-width: 768px) {
    margin-bottom: 0.6em;
  }
}

.project__image {
  display: block;
  grid-column: 1 / -1;
  grid-row: 1 / 3;
  width: 100%;
  border-right: 50px solid #ffffff;
  margin-top: -3.55em;
  z-index: -1;

  @media screen and (min-width: 768px) {
    grid-column: 5 / -1;
    grid-row: 1 / span 1;
  }
  
  @media screen and (min-width: 1365px) {
    grid-column: 4 / -1;
  }
  
  @media screen and (min-width: 1500px) {
    margin-top: -10em;
  }
}

.project__description {
  position: relative;
  color: #3d3d3d;
  width: 100%;
  padding-bottom: 2em;

  @media screen and (min-width: 630px) {

  }
  
  @media screen and (min-width: 768px) {

  }

  @media screen and (min-width: 1200px) {
    width: 50%;
  }
}

.project__tech {
  margin-top: 3em;
  padding-bottom: 3em;

  @media screen and (min-width: 630px) {
 
  }
  
  @media screen and (min-width: 768px) {

  }
}

.project__tech__item {
  border-bottom: 1px solid #adadad;
  padding-bottom: 0.5em;
  margin-bottom: 1em;
  color: #1d1d1d;
}

.project__tech__icon {
  display: inline-block;
  float: right;
  margin-top: 0.8em;
}

.project__tech__icon svg {
  display: block;
  width: 16px;
  height: 16px;
}

.project__tech__icon path {
  display: block;
  width: 16px;
  height: 16px;
}

.project__arrows {
  position: absolute;
  top: -45px;
  right: 0;
}

.project__arrow {
  display: inline-block;
  width: 45px;
  height: 45px;
  text-align: center;
  line-height: 45px;
  font-weight: 800;
  text-decoration: none;
  margin-right: 0;
  float: left;
}

.project__arrow {
  background: #1d1d1d;
  color: #efefef;
}

.project__arrow:nth-of-type(1) {
  opacity: 0.5;
}